section.ToDoList {
    /* If this was height: 100%, it would be as tall as its container. AS TALL AS ITS CONTAINER (and btw, keep in mind that the container is as
    tall as the grid cell that contains it), NOT the remaining space taking into account that the container has two elements, 
    and this is one of them. That's why you want it to be shorter than its container. */
    height: 81.5%;
    display: flex;
    flex-direction: column;
    overflow: scroll;
}


